import { useEffect, useState } from "react";
import Modal from "../../../../Ui/Modal/Modal";
import style from "../../FeesStructure/FeesStructure.module.css";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import FormInput from "../../../../Ui/Input/FormInput";
import TileWrapper from "../../../../Ui/Tab/Tile/TileWrapper";
import TileCard from "../../../../Ui/Tab/Tile/TileCard";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import { useTranslation } from "react-i18next";
import FeeStatisticsBankFilter from "../../FeeStatistics/FeeStatisticsBankFilter";
import SetButton from "../../../../Ui/Button/SetButton";
import CreateButton from "../../../../Ui/Button/CreateButton";
import InstituteStudent from "../../../InstituteStudent/InstituteStudent";
import SelectMasterFeeHead from "../../FeesStructure/Structure/SelectMasterFeeHead";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import { useFinanceAddOtherFees } from "../../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
const AddOtherFees = ({ onClose, fid, instituteId, onRefetch }) => {
  const { t } = useTranslation();
  const [step, setStep] = useState(0);
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);

  const [state, setState] = useState({
    students: [],
    other_fees_name: "",
    other_fees_type: "",
    bank_account: "",
  });
  const [totalFeeHeadAmount, setTotalFeeHeadAmount] = useState(0);
  const [addApplicableFeeHead, setAddApplicableFeeHead] = useState([]);
  const [applicableFeeHeadId, setApplicableFeeHeadId] = useState([]);

  const [financeAddOtherFees] = useFinanceAddOtherFees();
  useEffect(() => {
    if (addApplicableFeeHead) {
      let total = 0;
      for (let i = 0; i < addApplicableFeeHead?.length; i++) {
        total += +addApplicableFeeHead[i]?.head_amount;
      }
      setTotalFeeHeadAmount(total);
    }
  }, [addApplicableFeeHead]);
  const onTile = (value) => {
    setState((prev) => ({
      ...prev,
      other_fees_type: value,
    }));
  };
  const onInputChange = (e) => {
    setState((pre) => ({
      ...pre,
      [e.target.name]: e.target.value,
    }));
  };
  const onSelectBank = (value) => {
    setState((pre) => ({
      ...pre,
      bank_account: value,
    }));
  };
  const onFeeHeadLabelChange = (e, index) => {
    const input1 = [...addApplicableFeeHead];
    for (let inp = 0; inp < input1?.length; inp++) {
      if (inp === index) {
        input1[inp].head_name = e.target.value;
      }
    }
    setAddApplicableFeeHead(input1);
  };
  const onFeeHeadPriceChange = (e, index) => {
    const input1 = [...addApplicableFeeHead];
    for (let inp = 0; inp < input1?.length; inp++) {
      if (inp === index) {
        input1[inp].head_amount = +e.target.value;
      }
    }
    setAddApplicableFeeHead(input1);
  };

  const onStudent = () => {
    setStep(1);
  };

  const onCloseStep = () => {
    setStep(0);
  };
  const onSelectStudent = (val) => {
    if (state?.students?.includes(val?._id)) {
      setState((pre) => ({
        ...pre,
        students: pre?.students?.filter((id) => id !== val?._id),
      }));
    } else {
      setState((pre) => ({
        ...pre,
        students: [...pre?.students, val?._id],
      }));
    }
  };

  const onAddOtherFees = () => {
    let validation = requiredField(state, ["students"]);
    if (
      existingRequiredField(validation) &&
      fid &&
      addApplicableFeeHead?.length > 0 &&
      state?.students?.length > 0
    ) {
      setDisabled((pre) => !pre);
      financeAddOtherFees({
        fid: fid,
        bodyParameter: {
          ...state,
          bank_account: state?.bank_account?._id,
          heads: addApplicableFeeHead,
          payable_amount: totalFeeHeadAmount,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch(() => {
          setDisabled((pre) => !pre);
        });
    } else {
      setErrorField(validation);
    }
  };
  return (
    <>
      {step === 0 ? (
        <Modal onClose={() => {}}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("add_other_fees")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
                marginBottom: "1rem",
              }}
            >
              <FormInput
                label={t("fees_name")}
                placeholder={t("fees_name_placeholder")}
                name={"other_fees_name"}
                value={state.other_fees_name}
                type={"text"}
                onChange={onInputChange}
                errorShow={errorField["other_fees_name"]}
              />
              <TileWrapper
                label="fees_type"
                customTileContainer={{
                  flexWrap: "wrap",
                }}
                errorShow={errorField["other_fees_type"]}
              >
                {oFeeType?.map((tile) => (
                  <TileCard
                    key={tile?.key}
                    tile={tile}
                    onTile={onTile}
                    activeTile={state.other_fees_type}
                  />
                ))}
              </TileWrapper>
              <FeeStatisticsBankFilter
                fid={fid}
                onSelect={onSelectBank}
                errorShow={errorField["bank_account"]}
                viewAs="NORMAL_FILTER"
                defalutValue={
                  state?.bank_account?.finance_bank_account_name ?? ""
                }
              />
              {state.other_fees_type ? (
                <div
                  style={{
                    paddingInline: "1rem",
                    marginTop: "1rem",
                  }}
                >
                  <section
                    className={style.table_container}
                    style={{
                      margin: "0",
                    }}
                  >
                    <div className={style.table_heading}>
                      <h6>{t("fee_heads")}</h6>
                      <h6>{t("amount")}</h6>
                    </div>
                    <div className={style.table_body}>
                      {addApplicableFeeHead?.map((afh, index) => (
                        <div className={style.table_body_list} key={index}>
                          <input
                            type={"text"}
                            value={afh?.head_name}
                            onChange={(e) => onFeeHeadLabelChange(e, index)}
                            className={style.fee_head_name_input}
                          />
                          <input
                            type={"number"}
                            value={afh?.head_amount}
                            onChange={(e) => onFeeHeadPriceChange(e, index)}
                            className={style.fee_head_amount_input}
                          />
                        </div>
                      ))}
                    </div>
                    <button
                      className={style.add_fees_head_btn}
                      title="Add Fees"
                      onClick={() => setStep(2)}
                      style={{
                        cursor: "pointer",
                        width: "106.4%",
                      }}
                    >
                      {t("add")}
                      <img
                        src={`${assestsUserFinanceUrl}/plus.svg`}
                        alt="add"
                      />
                    </button>
                    <div className={style.table_body}>
                      <div
                        className={style.table_body_list}
                        style={{
                          borderTopColor: "rgba(18, 18, 18, 0.5)",
                          borderTopWidth: "0.5px",
                          borderTopStyle: "solid",
                        }}
                      >
                        <h6 className={style.fees_total}>{t("subtotal")}</h6>
                        <h6 className={style.fees_total}>
                          {totalFeeHeadAmount}
                        </h6>
                      </div>
                    </div>
                  </section>
                </div>
              ) : null}

              <SetButton
                label="add_student"
                count={state?.students?.length}
                onAction={onStudent}
              />
              <CreateButton label="save" onAction={onAddOtherFees} />
            </UserMemberContentWrapper>
            {disabled && <QvipleLoader />}
          </div>
        </Modal>
      ) : step === 1 ? (
        <InstituteStudent
          title={t("select_student")}
          onClose={onCloseStep}
          instituteId={instituteId}
          isMultiSelect
          onMultiSelect={onSelectStudent}
          onDone={onCloseStep}
          selectList={state.students}
        />
      ) : step === 2 ? (
        <SelectMasterFeeHead
          fid={fid}
          onClose={onCloseStep}
          setFeeHead={setAddApplicableFeeHead}
          setFeeHeadId={setApplicableFeeHeadId}
          feeHeadId={applicableFeeHeadId}
          isSegment="BY_OTHER_FEES"
          isOne={state.other_fees_type === "Normal Other Fees" ? true : false}
        />
      ) : null}
    </>
  );
};

export default AddOtherFees;

const oFeeType = [
  {
    key: "Normal Other Fees",
    name: "Normal Other Fees",
  },
  {
    key: "Subject Other Fees",
    name: "Subject Other Fees",
  },
];
